<template>
  <div>
    <Width100 v-bind:addPadding="true">
      <Width50>
        <Paragraph
          subtitle="Purpose and Context"
          paragraph="Fitted is a project I worked on for CareerFoundry’s UI for UX course. I was required to create a responsive web app based on certain feature requirements, brand and style guidelines, key messaging,  and user stories. This project had a duration of 2 months."
        />
        <Paragraph
          subtitle="What is Fitted?"
          paragraph="Fitted is a responsive web app aimed to motivate individuals into a fitness routine that fits their fitness level, schedule and interests."
        />
        <Paragraph
          subtitle="The Problem"
          paragraph="Finding exercise routines for your level can be difficult, especially if you want to try something new. With busy everyday schedules, finding routines that can fit into your schedule is not easy."
        />
        <Paragraph
          subtitle="The Goal"
          paragraph="Motivate people into an exercise routine that suits their level, schedule, and interests."
        />
        <Paragraph
          subtitle="Tools"
          paragraph="Marvelapp (Prototype), Sketch (Wireframes and Visual Design)"
        />
      </Width50>
      <Width50>
        <div class="marvel-container">
          <iframe src="https://marvelapp.com/1g2dd3hg?emb=1&iosapp=false&frameless=false" title="Prototype" width="100%" height="784" allowTransparency="true" frameborder="0"></iframe>
        </div>
      </Width50>
    </Width100>
    <LineBreak />
  </div>
</template>

<script>
import LineBreak from '../common/LineBreak';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    LineBreak,
    Paragraph,
    Width50,
    Width100,
  },

};
</script>

<style lang="scss" scoped>
.marvel-container {
  margin: 0 auto;
  text-align: center;
}
</style>



