<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        :title="title"
        :subtitle="subtitle"
        :paragraph="paragraph"
      />
    </Width100>
    <Width100>
      <VueImage :src="imgSrc" :alt="alt" :isExpandable="true"  />
    </Width100>
    <LineBreak v-if="!hideLine" />
  </div>
</template>

<script>
import LineBreak from './LineBreak';
import Paragraph from './Paragraph';
import VueImage from './VueImage';
import Width100 from './layout/Width100';

export default {
  components: {
    LineBreak,
    Paragraph,
    VueImage,
    Width100,
  },
  props: {
    title: {
      type: String
    },
    subtitle: {
      type: String
    },
    paragraph: {
      type: String
    },
    imgSrc: {
      type: String
    },
    hideLine: {
      type: Boolean
    },
    alt: {
      type: String,
      default: ""
    }
  },
};
</script>

<style lang="scss" scoped>

</style>



