<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Applying UX to UI"
        paragraph="For this case study project, I was given a brief that included a persona, user stories, and a list of feature requirements by CareerFoundry beforehand."
      />
    </Width100>

    <Width100>
      <Paragraph
        v-bind:center="true"
        subtitle="User Research"
        paragraph="However, I wanted to listen to real users who exercise or use fitness applications, I did extra user research before moving onto user flow creation and wireframing to make the product as useful as possible."
      />
    </Width100>

    <Width100>
      <Paragraph
        v-bind:center="true"
        paragraph="I conducted user survey which was answered by 10 respondents to gather more opinions and insights."
      />
    </Width100>

    <Width100>
      <CenteredText class="link">
        <div class="link_container">
          <AnchorButton
          text="Fitness Survey"
          href="https://docs.google.com/forms/d/e/1FAIpQLScxBLeW2lJI7b8rXwbvlWavGqNQ1088IMpCtG_20BRLMmEO9A/viewform?usp=sf_link"
          />
        </div>
      </CenteredText>
    </Width100>

    <Width100>
      <OrderedList
        title="Key Insights"
        titleColor="#FF8C00"
        v-bind:items="keyInsights"
      />
    </Width100>

    <LineBreak />
  </div>
</template>

<script>
import CenteredText from '../common/CenteredText';
import AnchorButton from '../common/AnchorButton';
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    AnchorButton,
    CenteredText,
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
      observations: [
        "People who are new or returning to fitness want to find activities they like, and get into a good routine.",
        "Users like to search and view routines, guides, daily challenges, and other information on any device. They want to also keep a schedule by adding sessions to their personal calendar.",
        "Users aim to get into a routine that suits them and need an app they can use whenever they like.",
        "When on the go or when practicing exercise routines at home, in the park, on the street, users want to stay fit anywhere."
      ],
      keyInsights: [
        "Users want to become healthy and enjoy the associated benefits (better mood, weight management, reduce risks of illness, learning something new).",
        "Users want to know how long they have worked out.",
        "Users want to check how many calories they have eaten.",
        "Users want a description of each exercise by coaching or video tutorials.",
        "Users want to be provided with exercising plans, routines or daily challenges within the application so that they do not need to make the plans by themselves from scratch.",
      ],
    };
  }

};
</script>

<style lang="scss" scoped>

.link_container {
  margin: 16px auto;
}

.link {
  text-align: center;
}

</style>



