<template>
  <ParagraphWithImage
    :imgSrc="imgSrc"
    :title="title"
    :paragraph="paragraph"
    alt="User flows"
  />
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';

import fitted_userflow1 from "../../assets/img/fitted_userflow1.jpg";

export default {
  components: {
    ParagraphWithImage,
  },
  data() {
    return {
      imgSrc: fitted_userflow1,
      title: "User Flows",
      paragraph: "After setting up the features, I made user flows to see the steps that the users will need to take to achieve their goals. The flow creation process was also helpful to think of ways to categorise and place each feature.",
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



