<template>
  <div>
    <ParagraphWithImage
      :imgSrc="imgSrc"
      :title="title"
      alt="Persona"
      :hideLine="true"
      paragraph="Based on the insights gathered by user research, I added more personal details to Rebecca ( persona provided by CareerFoundry)."
    />

    <Width100>
      <Paragraph
        v-bind:center="true"
        paragraph="I knew I wanted my app to be clean and minimalistic while incorporating  energizing colors, aspirational images, and a casual, motivating tone. As a tech savvy and busy developer, Rebecca will appreciate an intuitive and interacting web app. Because of the minimalistic design, the few bright colors will be eye catching and the aspirational images will motivate her use the app to achieve her goals."
      />
    </Width100>

    <LineBreak />
  </div>
</template>

<script>
import ParagraphWithImage from '../common/ParagraphWithImage';
import Paragraph from '../common/Paragraph';
import Width100 from '../common/layout/Width100';
import LineBreak from '../common/LineBreak';

import fitted_persona from "../../assets/img/fitted_persona.jpg";

export default {
  components: {
    ParagraphWithImage,
    Paragraph,
    LineBreak,
    Width100,
  },
  data() {
    return {
      imgSrc: fitted_persona,
      title: "Persona",
    };
  }

};
</script>

<style lang="scss" scoped>
</style>



