<template>
  <Width100>
    <VueImage :src="banner" alt="Landing banner" :isExpandable="true"  />
  </Width100>
</template>

<script>
import VueImage from '../common/VueImage';
import Width100 from '../common/layout/Width100';

import banner from "../../assets/img/fitted_header1.png";

export default {
  components: {
    VueImage,
    Width100,
  },
  data() {
    return {
      banner: banner,
    }
  }
};
</script>

<style lang="scss" scoped>
</style>



