<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Final Design"
      />
    </Width100>

    <Carousel
      v-bind:items="items"
      :containerStyle="containerStyle"
      :imgStyle="imgStyle"
    />
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import Paragraph from '../common/Paragraph';
import LineBreak from '../common/LineBreak';
import Width100 from '../common/layout/Width100';

import fitted_mockup4 from "../../assets/img/fitted_mockup4.png";
import fitted_mockup2 from "../../assets/img/fitted_mockup2.png";
import fitted_mockup3 from "../../assets/img/fitted_mockup3.png";
import fitted_mockup1 from "../../assets/img/fitted_mockup1.png";

export default {
  components: {
    Carousel,
    Paragraph,
    LineBreak,
    Width100,
  },
  data() {
    return {
      items: [
        {
          img: fitted_mockup4,
          alt: "Fitted Abstract Art",
        },
        {
          img: fitted_mockup3,
          alt: "Fitted Abstract Art",
        },
        {
          img: fitted_mockup2,
          alt: "Fitted Abstract Art",
        },
        {
          img: fitted_mockup1,
          alt: "Fitted Abstract Art",
        },
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



