<template>
  <ParagraphWithCarousel
    title="Responsive Design"
    paragraph="I created the wireframes for multi devices - mobile, tablet, and desktop version since they represent the vast majority of devices. I created 3 sets of designs using the 12 column grid system and default breakpoints."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import fitted_homescreen_breakpoints from "../../assets/img/fitted_homescreen_breakpoints.jpg";
import fitted_calendar_breakpoints from "../../assets/img/fitted_calendar_breakpoints.jpg";
import fitted_exercises_breakpoints from "../../assets/img/fitted_exercises_breakpoints.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: fitted_homescreen_breakpoints, alt: "Breakpoints of the page"},
        {img: fitted_exercises_breakpoints, alt: "Breakpoints of the page"},
        {img: fitted_calendar_breakpoints, alt: "Breakpoints of the page"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



