<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Design Challenge"
        paragraph="Since Fitted was a web application and not a native application, more careful design decisions had to be put on Fitted. Also, it was an invaluable opportunity to understand the strength and limitation of web applications during the design and testing process."
      />
    </Width100>

    <LineBreak />

    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Next Steps"
        paragraph="I would like to do more research and meet and listen to more users to gather more insights regarding what more features they want. By doing extra research, i can deliver a product as useful as possible."
        classnames="next_steps"
      />
    </Width100>

  </div>
</template>

<script>
import CenteredText from '../common/CenteredText';
import AnchorButton from '../common/AnchorButton';
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

export default {
  components: {
    AnchorButton,
    CenteredText,
    LineBreak,
    OrderedList,
    Paragraph,
    Width50,
    Width100,
  },
  data() {
    return {
    };
  }

};
</script>

<style lang="scss" scoped>
.next_steps {
  margin-bottom: 32px;
}
</style>



