<template>
  <div>
    <Width100>
      <Paragraph
        v-bind:center="true"
        title="Visual Direction"
        subtitle="Setting the mood"
        paragraph="After creating the wireframes for each feature, I made a mood board for the visual direction / design guidelines and proper branding of Fitted. I chose vivid colors orange and purple as a main colour of Fitted to show activeness and happiness of exercising, not giving users too much pressure about getting fitted."
      />
    </Width100>
    <Width100>
        <div class="moodboard_img">
            <VueImage :src="moodboard1" alt="Mood Board" :isExpandable="true"  />
        </div>
    </Width100>
  
    <LineBreak />
  </div>
</template>

<script>
import Carousel from '../common/Carousel';
import LineBreak from '../common/LineBreak';
import OrderedList from '../common/OrderedList';
import Paragraph from '../common/Paragraph';
import VueImage from '../common/VueImage';
import Width50 from '../common/layout/Width50';
import Width100 from '../common/layout/Width100';

import moodboard1 from "../../assets/img/moodboard1.jpg";

export default {
  components: {
    LineBreak,
    Paragraph,
    VueImage,
    Width100,
  },
  data() {
    return {
      moodboard1: moodboard1,
    };
  }

};
</script>

<style lang="scss" scoped>
.moodboard_img {
    margin: 16px 0;
}
</style>



