<template>
  <ParagraphWithCarousel
    title="User Stories"
    subtitle="Sketching & Brainstorming"
    paragraph="Based on the user flow diagram, I sketched out mobile screens that would meet the user stories' requirements."
    :carouselStyle="containerStyle"
    :carouselItems="items"
    :imgStyle="imgStyle"
  />
</template>

<script>
import ParagraphWithCarousel from '../common/ParagraphWithCarousel';

import fitted_sketch_1 from "../../assets/img/fitted_sketch_1.jpg";
import fitted_sketch_2 from "../../assets/img/fitted_sketch_2.jpg";
import fitted_sketch_3 from "../../assets/img/fitted_sketch_3.jpg";
import fitted_sketch_4 from "../../assets/img/fitted_sketch_4.jpg";
import fitted_sketch_5 from "../../assets/img/fitted_sketch_5.jpg";
import fitted_sketch_6 from "../../assets/img/fitted_sketch_6.jpg";

export default {
  components: {
    ParagraphWithCarousel,
  },
  data() {
    return {
      items: [
        {img: fitted_sketch_1, alt: "User Stories"},
        {img: fitted_sketch_2, alt: "User Stories"},
        {img: fitted_sketch_3, alt: "User Stories"},
        {img: fitted_sketch_4, alt: "User Stories"},
        {img: fitted_sketch_5, alt: "User Stories"},
        {img: fitted_sketch_6, alt: "User Stories"},
      ],
      containerStyle: {
        height: "100%"
      },
      imgStyle: {
        padding: "24px",
        width: "80%",
        margin: "0 auto",
      },
    };
  }

};
</script>

<style lang="scss" scoped>

</style>



